<template>
	<div id="store-setting">
		<public-header :title="store.title"></public-header>
		<public-footer :menufooter="menufooter"></public-footer>
		<div class="content">
			<div class="header">
				<div class="logo">
					<img :src="store.logo" alt="">
				</div>
				<div class="title">{{store.title}}</div>
			</div>
			<van-cell-group class="margin-10-b">
				<van-cell is-link :to="util.getUrl({path: '/plugin/pages/paotui/store/shop/select'})">
					<div slot="title" class="flex">
						<div class="img">
							<img src="static/img/cut_shop.png" alt="">
						</div>
						<div>切换门店</div>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-b">
				<van-cell is-link :to="util.getUrl({path: '/plugin/pages/paotui/store/shop/basic'})">
					<div slot="title" class="flex">
						<div class="img">
							<img src="static/img/shop_info.png" alt="">
						</div>
						<div>门店信息</div>
					</div>
				</van-cell>
				<van-cell v-if="0" is-link :to="util.getUrl({path: '/plugin/pages/paotui/store/shop/settingMore'})">
					<div slot="title" class="flex">
						<div class="img">
							<img src="static/img/auto_takeOrder.png" alt="">
						</div>
						<div>模板消息/语音电话</div>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group>
				<van-cell is-link :to="util.getUrl({path: '/plugin/pages/paotui/store/shop/password'})">
					<div slot="title" class="flex">
						<div class="img">
							<img src="static/img/account_password.png" alt="">
						</div>
						<div>账户密码</div>
					</div>
				</van-cell>
			</van-cell-group>
			<!--<div class="padding-15" @click="onLoginout()">
				<van-button size="normal" class="bg-danger font-16">退出</van-button>
			</div>
			<div class="manager-tips">语音技术由科大讯飞提供支持</div>-->
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import publicFooter from "@/components/footer"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				store: {
					title: '',
				},
				manager: {},
				stat: {},
				notice: 30,
				order_notice_num: 0,
				showPreLoading: true,
				menufooter: {}
			}
		},
		components: {
			publicHeader,
			publicFooter,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'errander/store/info',
					data: {
						menufooter: 1
					},
					success: function(res) {
						that.store = that.util.extend(that.store, res.store);
						that.manager = res.manager;
						that.stat = res.stat;
						that.notice = res.notice;
						that.order_notice_num = res.order_notice_num;
					}
				});
			},

			onLoginout() {
				this.$dialog.confirm({
					title: '温馨提示',
					message: '确定退出登录吗？'
				}).then(() => {
					this.util.removeStorage('clerkInfo');
					if(this.util.isCloud()) {
						api.removePrefs({
							key: 'clerkInfo'
						});
					}
					this.$router.replace(this.util.getUrl({path: '/pages/home/index'}));
					return;
				})
			}
		},
		created() {
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#store-setting .content .header{
		background-color: #ff2d4b;
		text-align: center;
		color: #fff;
		padding: 10px 0;
	}
	#store-setting .content .header .logo{
		width:60px;
		height:60px;
		border-radius: 50%;
		border:2px solid #fff;
		margin:0 auto;
		font-size:0;
	}
	#store-setting .content .header .logo img{
		width: 100%;
		height: 100%;
		border-radius:50%;
	}
	#store-setting .content .header .title{
		width:100%;
		box-sizing:border-box;
		padding:0 15px;
		margin-top:10px;
		font-size:15px;
		overflow:hidden;
		text-overflow:ellipsis;
		white-space:nowrap;
	}
	#store-setting .content .van-button{
		width: 100%;
	}
	#store-setting .content .van-cell .img{
		width: 25px;
		height: 25px;
		margin-right: 10px;
	}
	#store-setting .content .van-cell .img img{
		width: 100%;
		height: 100%;
	}
	#store-setting .manager-tips{
		font-size: 14px;
		text-align: center;
		margin-top: 5px;
		color: #999;
	}
</style>


